export default{
	props:{
		isshow:0,
		title:"",
		imgurl:"",
		link:"",
		desc:""
	},
	data:function(){
		return {
			skyShareShow:0,
			shareImg:"",
			shareList:[],
			isH5:false
		}
	},
	 
	created:function(){
		var that=this;
		if(this.imgurl!='' && this.imgurl!=undefined){
			this.shareImg=this.imgurl+".100x100.jpg";
		}else{
			this.shareImg=this.app.apiHost+"/static/logo.jpg";
		}










	},
	
	methods:{
		shareHide:function(){
			this.skyShareShow=0;
		},
		shareWeixin:function(){
			var that=this;
			uni.share({
				provider: "weixin",
				scene: "WXSceneSession",
				type: 0,
				href: that.link,
				title: that.title,
				summary: that.desc,
				imageUrl: that.shareImg,
				success: function (res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function (err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		shareWxzone:function(){
			var that=this;
			uni.share({
				provider: "weixin",
				scene: "WXSenceTimeline",
				type: 0,
				href: that.link,
				title: that.title,
				summary: that.desc,
				imageUrl: that.shareImg,
				success: function (res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function (err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		shareQQ:function(){
			var that=this;
			uni.share({
				provider: "qq",
				scene: "WXSenceTimeline",
				type: 0,
				href: that.link,
				title: that.title,
				summary: that.desc,
				imageUrl: that.shareImg,
				success: function (res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function (err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		}
		
	}
}
